<template>
    <div>
        <publicMaskPopup ref="upgradeRef" isCenter>
            <template #slot_mask>
                <div class="upgradeMain">
                    <i class="iconfont icon-guanbi" @click="upgradeRef.maskHide"></i>
                    <div class="mainText">
                        <div class="textLine">
                            <p class="label">step</p>
                            <p class="level">step{{ data.phaseIndex }}</p>
                        </div>
                        <div class="textLine">
                            <p class="label">{{ t("User.txt15") }}</p>
                            <p class="value">{{ formatNumberWithCurrency(data.amount) }}</p>
                        </div>
                        <div class="textLine">
                            <p class="label">{{ t("User.txt3") }}</p>
                            <p class="value">{{ data.platform }}</p>
                        </div>
                        <div class="textLine">
                            <p class="label">{{ t("User.txt4") }}</p>
                            <p class="value">{{ data.account }}</p>
                        </div>
                        <!-- 第一次提現展示可返現費用：提现次数（withdrawCount）為0時代表第一次提現 -->
                        <div class="textLine" v-if="!data.withdrawCount && data.phaseIndex === 3">
                            <p class="label">{{ t("common.fee") }}</p>
                            <p class="value">{{ formatNumberWithCurrency(levelFee[data.level-1]) }}</p>
                        </div>
                        <div class="textLine" v-if="data.phaseIndex === 3">
                            <p class="label">{{ t("User.txt16") }}</p>
                            <p class="value">{{ totalAmount }}</p>
                        </div>
                    </div>
                    <p class="tips">{{ t(levelTipsStr, { ratio: data?.ratioStr }) }}</p>
                    <button class="btn" @click="bindDoUpGrade"
                        :class="{ disabled: data.phaseIndex === 3 && data.amount < 100 }">
                        {{ data.phaseIndex === 3 ? t("User.txt56") : t("Login.btn2") }}
                    </button>
                    <div class="disabled-tips" v-if="data.phaseIndex === 3 && data.amount < 100">{{ t('User.txt17') }}</div>
                </div>
            </template>
        </publicMaskPopup>
        <!---挑战2阶段升3阶段实名认证弹窗-->
        <publicMaskPopup ref="authenticatedRef" isCenter :isClickMaskHide="false">
            <template #slot_mask>
                <div class="authenticatedMain">
                    <img class="close" alt="" src="@/assets/images/icons/close.png"
                        @click="authenticatedRef.maskHide" />
                    <template v-if="kycStatus === '0'">
                        <p class="text">{{ t("User.tips1") }}</p>
                        <p class="text">{{ t("User.tips2") }}</p>
                    </template>
                    <template v-else-if="kycStatus === '1'">
                        <p class="text">{{ t("User.tips15", { time: 10 }) }}</p>
                    </template>
                    <template v-else-if="kycStatus === '3'|| kycStatus === '5'">
                        <p class="text">{{ t("User.tips16") }}</p>
                        <p class="text">{{ t("User.tips17", { reason: kycFailReason }) }}</p>
                    </template>
                    <template v-else-if="kycStatus === '4'">
                        <p class="text" style="text-align: center">{{ t("User.cancelVerify") }}</p>
                    </template>
                    <div class="btnGroup">
                        <button class="btn" @click="bindKycVerify">
                            <span v-if="kycStatus === '0'">{{ t("User.btn1") }}</span>
                            <span v-if="kycStatus === '1'">{{ t("User.btn8") }}</span>
                            <span v-if="kycStatus === '3' || kycStatus === '4'|| kycStatus === '5'">{{ t("User.btn9") }}</span>
                        </button>
                        <button class="btn light" @click="bindKycClose" v-if="kycStatus === '1'">
                            {{ t("User.close") }}
                        </button>
                    </div>
                </div>
            </template>
        </publicMaskPopup>
        <!---挑战3阶段提现金额不足提示-->
        <publicTipsPopup ref="publicTipsRef" :content="t('User.tips22')" :isBtnGroup="false"></publicTipsPopup>
    </div>
</template>
<script setup>
import { computed, toRefs } from "vue";
import { bindSettlement } from "@/api/challenge";
import { formatNumberWithCurrency, multiply } from "@/utils/tools";
import { useUserInfo } from "@/store/user";
import { getKycStatus, getKycVerify } from "@/api/kyc";
import { useAppConfig } from "nuxt/app";
const { t } = useI18n();
const props = defineProps({
    data: {
        type: Object,
        default: {}
    }
});
const levelFee = ref([99,500,999,1699,2499])
const { data } = toRefs(props);
const kycStatus = ref();
const kycFailReason = ref("");
const upgradeRef = ref();
const authenticatedRef = ref();
const publicTipsRef = ref();
const submitBol = ref(false);
const ugBol = ref(false);
const levelTipsStr = ref("User.tips8");
// 可提现金额大于$100 和 第一次出金退回报名费
const totalAmount = computed(() => {
    return !data.value.withdrawCount ? formatNumberWithCurrency(data.value.amount + levelFee.value[data.value.level-1]) : formatNumberWithCurrency(data.value.amount)
})
const shower = () => {
    upgradeRef.value.maskShow();
    if (data.value.phaseIndex === 1) levelTipsStr.value = "User.tips8";
    if (data.value.phaseIndex === 2) levelTipsStr.value = "User.tips13";
    if (data.value.phaseIndex === 3) {
        levelTipsStr.value = "User.tips14";
        data.value.ratioStr = `${multiply(data.value.profitSharingRatio, 10)}%`;
    }
    const userUid = useUserInfo().getInfo().data.userAccount;
    getKycStatus({ uid: userUid }).then(res => {
        /*
    0-未认证 用户还没有开始审核
    1-审核中,待处理
    2-通过
    3-失败
    4-审核取消 
    5-超时
    */
        kycStatus.value = res.data.status;
        kycFailReason.value = res.data.status === '5'?'Authentication timeout':res.data.declinedInfo;
    });
};
defineExpose({ shower });
const emits = defineEmits(["reFlashList"]);
const bindDoUpGrade = () => {
    //升级按钮
    const params = {
        tradingChallengeId: data.value.tradingChallengeId,
        orderNo: data.value.orderNo,
        withdrawType: data.value.withdrawType,
        amount: data.value.amount,
        email: "",
        level: data.value.phaseIndex === 1 ? 2 : 3
    };
    if (data.value.phaseIndex === 2 && kycStatus.value !== "2") {
        authenticatedRef.value.maskShow();
    } else if (data.value.phaseIndex === 3 && data.value.amount < 100) {
        publicTipsRef.value.shower();
    } else {
        upGrade(params);
    }
};
const upGrade = _debounce(
    data => {
        //升级接口
        const loading = ElLoading.service({
            lock: true,
            text: "Loading",
            background: "rgba(0, 0, 0, 0)"
        });
        if (!submitBol.value) {
            submitBol.value = true;
            bindSettlement(data)
                .then(res => {
                    submitBol.value = false;
                    upgradeRef.value.maskHide();
                    emits("reFlashList");
                    loading.close();
                })
                .catch(err => {
                    submitBol.value = false;
                    loading.close();
                });
        }
    },
    1000,
    { leading: true, trailing: false }
);
const bindKycClose = () => {
    //关闭按钮
    authenticatedRef.value.maskHide();
    upgradeRef.value.maskHide();
};
const bindKycVerify = () => {
    //  认证/重新认证按钮
    if (!ugBol.value) {
        ugBol.value = true;
        const params = {
            uid: useUserInfo().getInfo().data.userAccount,
            verificationUrl: useAppConfig().public.hostUrl + "/user"
        };
        getKycVerify(params)
            .then(res => {
                ugBol.value = false;
                authenticatedRef.value.maskHide();
                window.open(res.data.verificationUrl, "_blank");
            })
            .catch(err => {
                ugBol.value = false;
            });
    }
};
</script>
<style scoped lang="scss">
.upgradeMain {
    width: 414px;
    height: auto;
    background-color: #000000;
    border-radius: 24px;
    padding: 42px 34px;
    box-sizing: border-box;

    .icon-guanbi {
        @include setfont(14px, #575757);
        position: absolute;
        top: 16px;
        right: 16px;
        cursor: pointer;
    }

    .mainText {
        .textLine {
            width: 100%;
            @include dfjsbac;
            margin-top: 10px;

            &:first-child {
                margin-top: 0;
            }

            .label {
                @include setfont(14px, #979797, 400);
                line-height: 21px;
            }

            .value {
                @include setfont(14px, #ffffff, 500);
                line-height: 21px;
            }

            .level {
                width: auto;
                height: 23px;
                background: rgba(#FF720E, 0.2);
                border-radius: 10px;
                padding: 0 14px;
                @include setfont(12px, #FF720E, 700);
                @include dfac;
            }
        }
    }

    .tips {
        @include setfont(12px, #979797, 500);
        line-height: 18px;
        margin-top: 50px;
        white-space: pre-line;
    }

    .btn {
        @extend %bg_btn;
        width: auto;
        height: 42px;
        padding:0 20px;
        border-radius: 5em;
        margin: 35px auto 0;
        @include setfont(16px, #ffffff, 700);
        cursor: pointer;
        display: block;

        &.disabled {
            background: #595959;
            color: rgba(#fff, .5);
            pointer-events: none;
        }
    }

    .disabled-tips {
        @include setfont(12px, #FF4A37, 400);
        margin-top: 20px;
        text-align: center;
    }
}

.authenticatedMain {
    width: 518px;
    height: auto;
    background-color: #000000;
    border-radius: 24px;
    padding: 64px 64px 46px;
    box-sizing: border-box;

    .close {
        width: 22px;
        height: 22px;
        position: absolute;
        top: 30px;
        right: 30px;
        cursor: pointer;
    }

    .text {
        @include setfont(14px, #ffffff, 500);
        line-height: 21px;
        padding-bottom: 20px;
    }

    .btnGroup {
        margin-top: 26px;
        @include dfjc;
    }

    .btn {
        @extend %bg_btn;
        width: 158px;
        height: 42px;
        border-radius: 5em;
        margin: 0 10px;
        @include setfont(16px, #ffffff, 700);
        cursor: pointer;
        display: block;

        &.light {
            background: transparent;
            border: 1px solid #ff720e;
            color: #ff720e;
        }

        &.disabled {
            background: #595959;
            color: rgba(#fff, .5);
            pointer-events: none;
        }
    }
}

@media screen and (max-width: 1024px) {
    .upgradeMain {
        width: 87.4667vw;
        border-radius: 5.3333vw;
        padding: 13.3333vw 4.8vw 10.1333vw;

        .icon-guanbi {
            font-size: 4vw;
            top: 4vw;
            right: 4vw;
        }

        .mainText {
            .textLine {
                margin-top: 2.6667vw;

                .label {
                    font-size: 3.7333vw;
                    line-height: 5.6vw;
                }

                .value {
                    font-size: 3.7333vw;
                    line-height: 5.6vw;
                    max-width: 70%;
                }

                .level {
                    height: 5.8667vw;
                    border-radius: 1.0667vw;
                    padding: 0 1.3333vw;
                    font-size: 3.2vw;
                }
            }
        }

        .tips {
            margin-top: 5.3333vw;
            font-size: 3.2vw;
            line-height: 4.8vw;
        }

        .btn {
            width: 100%;
            height: 11.2vw;
            margin: 5.3333vw auto 0;
            font-size: 4.2667vw;
            box-sizing: border-box
        }

        .disabled-tips {
            @include setfont(12px, #FF4A37, 400);
            margin-top: 4.8vw;
            text-align: center;
            padding: 0 4.8vw;
            line-height: 1.3;
        }
    }

    .authenticatedMain {
        width: 87.4667vw;
        border-radius: 6.4vw;
        padding: 13.3333vw 4.8vw 10.1333vw;

        .close {
            width: 5.8667vw;
            height: 5.8667vw;
            top: 8vw;
            right: 8vw;
        }

        .text {
            font-size: 3.7333vw;
            line-height: 5.6vw;
            padding-bottom: 5.3333vw;
        }

        .btnGroup {
            margin-top: 9.3333vw;
        }

        .btn {
            width: 42.1333vw;
            height: 11.2vw;
            margin: 0 4vw;
            font-size: 4.2667vw;
        }
    }
}
</style>
