import { onMounted, computed, ref } from "vue";
import { formatNumberWithCurrency } from "@/utils/tools.js"

/**
 * 
 * @param {Object} detailInfo 接口數據
 */
export const useChallengeDetails = (detailInfo = {}) => {
    // console.log(detailInfo, "detailInfodetailInfo")
    const { scalingPlan = {}, seniorPlan = {} } = detailInfo
    // summary： 通過 | 進行中
    const summaryStatus = (condition) => condition ? "Battle.tips5" : "User.list.stu1"
    // 擴展計劃階段
    const fundPlanStep = computed(() => `Level ${detailInfo?.scalingPlan?.expandPlansJoinCount}`)
    // checkExpandPlansConditionCode：1-不符合条件,2-符合条件,3-符合条件中
    const extractSummary = summaryStatus(scalingPlan?.checkExpandPlansConditionCode === "2")
    // 擴展計劃出金次数
    const extractTimesSummary = summaryStatus(scalingPlan?.extractTimesResult >= scalingPlan?.extractTimesTarget)
    // 擴展計劃出金总额
    const extractAmountSummary = summaryStatus(scalingPlan?.extractTotalAmountResult >= scalingPlan?.extractTotalAmountTarget)
    // 沒有數據時顯示"--"
    const seniorData = (condition, val) => condition ? val : "--"
    // 高級計劃維持時間summary
    const seniorKeepDaysSummary = summaryStatus(Math.floor(seniorPlan?.currentKeepDays / 30) >= Math.floor(seniorPlan.nextSeniorConfig?.profitDays / 30))
    // 扩展計劃維持時間summary
    const scalingPlanKeepDaysSummary = summaryStatus(Math.floor(scalingPlan?.profitDaysResult / 30) >= Math.floor(scalingPlan?.profitDaysTarget / 30))
    // 高级计划等级
    const seniorLev = (lev) => lev ? "User.txt32" : "User.txt14"
    const seniorLev1 = computed(() => seniorLev(seniorPlan.currentSeniorConfig !== null && seniorPlan.currentSeniorConfig.level > 0))
    const seniorLev2 = computed(() => seniorLev(seniorPlan.currentSeniorConfig !== null && seniorPlan.currentSeniorConfig.level > 1))
    // 當前是優享權益還是卓越權益
    const currentBenefits = computed(() => (seniorPlan.currentSeniorConfig !== null && seniorPlan.currentSeniorConfig.level < 1) ? "User.txt35" : "common.fund31")
    // 1,2階段滿足升級要求展示升級結算按鈕
    const btnUpgradeSettlement = computed(() => detailInfo.phaseIndex !== 3 && detailInfo.isUserUp)
    // 1,2階段不滿足升級要求展示MT5按鈕
    const btnMT5 = computed(() => detailInfo.phaseIndex !== 3 && !detailInfo.isUserUp)
    // 3 階段无论是否满足条件都可以点击确认结算按钮
    const btnSettlement = computed(() => detailInfo.phaseIndex === 3)
    // 高级计划升级按钮
    const btnUpgradeSenior = computed(() => {
        let list = premiumPlanList.value[premiumPlanList.value.length - 1].children
        return list.every(item => item.label === "Battle.tips5")
    })

    // 賬戶總規模金額 = 挑戰賬戶規模資金 + 擴展賬戶規模資金
    const totalAccountBalance = computed(() => parseFloat(detailInfo.initAccountFee))

    // 资金扩展計劃part1
    const fundPlan = ref([
        {
            label: "User.txt18",
            faq: ["User.txt19"],
            val: fundPlanStep
        },
        {
            label: "User.txt20",
            faq: ["User.txt21", "User.txt22"],
            // 擴展賬戶金額
            val: formatNumberWithCurrency(scalingPlan.expandPlansTotalAmount, 0)
        },
    ])
    // 资金扩展计划
    const fundPlanList = ref([{
        title: "User.txt42",
        children: [
            {
                label: "User.txt23",
                faq: ["User.txt24"]
            },
            {
                label: "User.txt25",
                faq: ["User.txt26"]
            },
            // {
            //     label: "User.txt41",
            // },
            {
                label: "Battle.txt6",
            },
        ]
    }, {
        title: "TARGET",
        children: [
            {
                label: ["common.fund53", Math.floor(scalingPlan.profitDaysTarget / 30), "num"],// 多語言傳參用數組
            },
            {
                label: scalingPlan.extractTimesTarget,
            },
            // {
            //     label: formatNumberWithCurrency(scalingPlan.extractTotalAmountTarget, 0),
            // },
            {
                label: "--",
            },
        ]
    }, {
        title: "RESULT",
        children: [{
            label: ["common.fund53", Math.floor(scalingPlan.profitDaysResult / 30), "num"],
        },
        {
            label: scalingPlan.extractTimesResult,
        },
        // {
        //     label: formatNumberWithCurrency(scalingPlan.extractTotalAmountResult, 0),
        // },
        {
            label: "--",
        }]
    },
    {
        title: "SUMMARY",
        children: [
            {
                label: scalingPlanKeepDaysSummary,
            },
            {
                label: extractTimesSummary,
            },
            // {
            //     label: extractSummary,
            // },
            {
                label: summaryStatus(detailInfo.positionResult === 0)
            }
        ]
    }])

    // 高級計劃
    const premiumPlan = ref([
        {
            label: "common.fund21",
            faq: ['common.fund23', 'common.fund24', 'common.fund25', 'common.fund26', "common.fund27", "common.fund29"],
            val: seniorLev1
        },
        {
            label: "common.fund31",
            faq: ["common.fund34", "common.fund35", "common.fund36", "common.fund37"],
            val: seniorLev2
        },
    ])

    const premiumPlanList = ref([{
        title: currentBenefits.value,
        children: [
            {
                label: "User.txt23",
                faq: ["User.txt24"]
            },
            {
                label: "User.txt25",
                faq: ["User.txt34"]
            },
            {
                label: "User.txt33",
                faq: ["User.txt34"]
            },
            {
                label: "common.fund8",
            },
            {
                label: "Battle.txt6",
            },
        ]
    }, {
        title: "TARGET",
        children: [
            {
                label: seniorData(seniorPlan.nextSeniorConfig, ["common.fund53", Math.floor(seniorPlan.nextSeniorConfig?.profitDays / 30), "num"])
            },
            {
                label: seniorData(seniorPlan.nextSeniorConfig, seniorPlan.nextSeniorConfig?.extractTimes),
            },
            {
                label: formatNumberWithCurrency((totalAccountBalance.value * 0.16), 0),
            },
            {
                label: "--",
            },
            {
                label: "--",
            }
        ]
    }, {
        title: "RESULT",
        children: [
            {
                label: ["common.fund53", Math.floor(seniorPlan?.currentKeepDays / 30), "num"],
            },
            {
                label: seniorData(seniorPlan?.currentWithdrawalCount, seniorPlan?.currentWithdrawalCount),
            },
            {
                label: formatNumberWithCurrency(seniorPlan?.currentWithdrawalAmount, 0),
            },
            {
                label: "--",
            },
            {
                label: "--",
            }
        ]
    },
    {
        title: "SUMMARY",
        children: [
            {
                label: seniorKeepDaysSummary,
            },
            {
                label: summaryStatus(seniorPlan?.seniorPlanCondition),
            },
            {
                label: summaryStatus(seniorPlan?.seniorPlanCondition),
            },
            {
                label: summaryStatus(seniorPlan?.profitStatus),
            },
            {
                label: summaryStatus(detailInfo.positionResult === 0),
            }
        ]
    },])

    // H5數據格式
    const h5DataFormat = (fundPlanList) => {
        const grouped = Array.from({ length: Math.max(...fundPlanList.map(item => item.children.length)) }, () => []);
        let labelArr = []
        fundPlanList.forEach(item => {
            labelArr.push(item.title)
            item.children.forEach((child, index) => {
                grouped[index].push(child);
            });
        });

        return { grouped, labelArr };
    }
    const fundPlanListH5 = ref(h5DataFormat(fundPlanList.value))
    const premiumPlanListH5 = ref(h5DataFormat(premiumPlanList.value))
    return {
        fundPlanStep,
        fundPlan,
        fundPlanList,
        premiumPlan,
        premiumPlanList,
        fundPlanListH5,
        premiumPlanListH5,
        totalAccountBalance,
        seniorLev1,
        seniorLev2,
        btnUpgradeSettlement,
        btnMT5,
        btnSettlement,
        btnUpgradeSenior
    }

}