<template>
    <clientOnly>
        <el-tooltip class="box-item" effect="dark" :content="formatFaq(txt?.faq)" :placement="placement"
            :append-to="appendClass" popper-class="aTips" v-if="txt?.faq">
            <span class="label hover" :style="{ color: labelColor }" :class="{ 'height-auto': heightAuto }">
                <img class="question" alt="" src="@/assets/images/challenges/doubt.svg" />
                {{ isChangeLang(txt?.label) }}
            </span>
        </el-tooltip>
        <div class="label" v-else>{{ isChangeLang(txt?.label) }}</div>
    </clientOnly>
</template>
<script setup>
import { ElTooltip } from 'element-plus'
import { computed } from 'vue';

const props = defineProps({
    txt: {
        type: Object,
        default: () => { }
    },
    appendClass: {
        type: String,
        default: '.challengeDetailMain'
    },
    alignR: {
        type: Boolean,
        default: false
    },
    heightAuto: {
        type: Boolean,
        default: false
    },
})
// tips出现的位置
const placement = computed(() => {
    const media = window.innerWidth;
    return media > 1024 ? 'right' : 'bottom-start'
})
const labelColor = computed(() => props.alignR ? "#fff" : "#979797")
const { t } = useI18n();
// 拼接多個提示語
const formatFaq = (arr) => {
    return arr.map(item => item.includes(".") ? t(item) : item).reduce((pre, cur) => {
        return `${pre}\n${cur}`
    })
}
// 判斷文案是否需要轉多語言(兼容$0.00情況)
const langCondition = (word) => typeof word === "string" && word.includes(".") && isNaN(Number(word.split('.')[1]))
const isChangeLang = (item) => {
    let res
    if (langCondition(item)) {
        res = t(item)
    } else if (item instanceof Array) {
        // 多語言傳參
        const [lang, val, key] = item
        let param = {}
        param[key] = val
        res = t(lang, param)
    } else {
        res = item
    }
    return res
}
</script>
<style scoped lang='scss'>
.label {
    display: flex;
    height: 20px;
    width: max-content;


    .question {
        margin-right: 3px;
        width: 12px;
        height: auto;
    }

    &.hover {
        cursor: pointer;
    }
}

@media screen and (max-width: 1024px) {
    .label {

        height: 4.8vw;
        line-height: 1.1;
        align-items: flex-start;
        font-size: 3.2vw;
        color: #979797;
        white-space: pre-line;

        .question {
            display: inline-block;
            margin-right: .8vw;
            width: 3.2vw;
            height: auto;
        }

        &.height-auto {
            height: auto;
        }
    }
}
</style>