<template>
    <publicMaskPopup ref="mergePopupRef" isCenter>
        <template #slot_mask>
            <div class="merge-popup">
                <p class="title">
                    {{ t('User.txt38') }}
                    <i class="close iconfont icon-guanbi" @click="close"></i>
                </p>
                <div class="merge-account">
                    <div class="header-title">
                        <span class="header-title-li" v-for="(txt, idx) in tHeaders">{{ txt }}</span>
                    </div>

                    <el-radio-group v-model="mainChallenge" text-color="#fff" fill="#FF7700" size="large"
                        @change="change">
                        <el-radio :value="idx" v-for="(item, idx) in tableData" :key="idx">
                            <div class="challenge1">
                                <span v-for="(prop) of item" class="li">{{ prop }}</span>
                            </div>
                        </el-radio>
                    </el-radio-group>
                </div>
                <div class="merge-account-h5">
                    <el-radio-group v-model="mainChallenge" text-color="#fff" fill="#FF7700" size="large"
                        @change="change">
                        <el-radio :value="idx" v-for="(item, idx) in tableData" :key="idx">
                            <div class="challenge-h5">
                                <div class="li" v-for="(prop, index) in item">
                                    <span class="l">{{ tHeadersh5[index] }}</span>
                                    <span class="r">{{ prop }}</span>
                                </div>
                            </div>
                        </el-radio>
                    </el-radio-group>

                </div>
                <div class="merge-btn" @click="mergeChallenge">{{ t('User.txt39') }}</div>
            </div>
        </template>

    </publicMaskPopup>
</template>
<script setup>
import { mergeTradeAccount } from '@/api/challenge'
import { ref } from 'vue';
import { formatNumberWithCurrency } from "@/utils/tools.js"
import { ElMessage } from 'element-plus'
const { t } = useI18n();
const mergePopupRef = ref()
const tHeaders = ref([t("User.list.th1"), "step", t("Battle.size"), t('User.txt55'), t("common.fund8"), t("Battle.txt6")])
const tHeadersh5 = ref({
    orderId: t("User.list.th1"),
    level: "step",
    accountSize: t("Battle.size"),
    currentEquity: t('User.txt55'),
    profitStatus: t("common.fund8"),
    currentPositionCount: t("Battle.txt6"),
},)
let mainChallenge = ref()
const tableData = ref([])
let mergeParams = ref({
    mergeMainOrderId: 0,
    mergeSubOrderId: 0,
    userId: "",
    challengeLevel: 3
})
const change = (val) => {
    let subIdx = val === 0 ? 1 : 0
    mergeParams.value.mergeMainOrderId = tableData.value[val]?.orderId
    mergeParams.value.mergeSubOrderId = tableData.value[subIdx]?.orderId
}
const show = (data) => {
    tableData.value = []
    mainChallenge.value = 0
    data?.map(item => {
        if (Object.keys(item).length > 1) {
            const { orderId, level, accountSize, currentEquity, currentPositionCount } = item;
            let res = {
                level: `step ${level}`,
                orderId,
                accountSize: formatNumberWithCurrency(accountSize, 0),
                currentEquity: formatNumberWithCurrency(currentEquity, 0),
                profitStatus: currentEquity > accountSize ? t("Battle.tips5") : t("User.list.stu1"),
                currentPositionCount
            }
            tableData.value.push(res)
            change(mainChallenge.value)
        }
    })
    mergePopupRef.value.maskShow()
};
const mergeChallenge = async () => {
    try {
        let res = await mergeTradeAccount(mergeParams.value)
        console.log(res, "合并");
        if(res.code == 200) {
            ElMessage.success(`${t('User.txt37')}!`)
            close()
        }
    } catch (err) {
        // console.log(err, "合并err");
        ElMessage.error(`${err}`)
        close()
    }
}
const close = () => mergePopupRef.value.maskHide()

defineExpose({ show, close });
</script>
<style scoped lang='scss'>
.merge-popup {
    padding: 30px 40px;
    border-radius: 24px;
    background: #000;
    @include setfont(22px, #ffffff, 700);
    line-height: 36px;
    text-align: center;
    position: relative;
    box-sizing: border-box;

    .close {
        display: inline-block;
        color: #66625D;
        cursor: pointer;

        position: absolute;
        right: 20px;
        top: 10px;
    }

    .merge-account {
        margin-top: 30px;

        .el-radio-group {
            display: flex;
            justify-content: space-between;
            flex-direction: column;
        }

        .el-radio {
            --el-radio-input-height: 12px;
            --el-radio-input-width: 12px;
            margin-right: 10px;
        }

    }

    .merge-account-h5 {
        display: none;
    }

    .challenge1 {
        width: 100%;
        display: flex;
        justify-content: space-between;

        .li {
            color: #fff;
            width: 200px;

            &:last-child {
                padding-right: 0;
            }

            &:first-child {
                padding-left: 5px;
                width: auto;
                min-width: 25px;
                text-align: left;
            }
        }
    }

    :deep(.el-radio__label) {
        width: 100%;
        font-size: 12px;
    }

    .header-title {
        width: 98%;
        color: #979797;
        font-size: 12px;
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        margin-left: 20px;

        &-li {
            width: calc(100% / 6);
            line-height: 1.2;
            margin-bottom: 10px;

            &:first-child {
                padding-left: 5px;
                width: auto;
                text-align: left;
                white-space: nowrap;
            }
        }
    }

    :deep(.el-radio__input.is-checked .el-radio__inner) {
        background: transparent;
        border-color: #FF7700;
    }

    :deep(.el-radio__inner::after) {
        background-color: #FF7700;
        width: 8px;
        height: 8px;
    }

    :deep(.el-radio__inner:hover) {
        border-color: #FF7700;
    }

    .merge-btn {
        box-sizing: content-box;
        width: max-content;
        min-width: 340px;
        cursor: pointer;
        background: linear-gradient(90deg, #FF7700 0%, #FF5847 100%);
        border-radius: 20px;
        color: #fff;
        margin: 40px auto 0;
        font-size: 16px;
    }
}

@media screen and (max-width: 1024px) {

    .merge-popup {
        width: calc(100vw - 12.8vw);
        padding: 4.2667vw;
        border-radius: 6.4vw;
        @include setfont(4.8vw, #ffffff, 700);
        line-height: 1.5;
        text-align: center;
        position: relative;
        box-sizing: border-box;
        margin: 0 auto;

        .title {
            margin: 0 auto 2.6667vw;
            max-width: 74%;
            height: auto;
        }

        .close {
            position: absolute;
            right: 5.3333vw;
            top: 5.3333vw;
            font-size: 3.7333vw;
        }

        .merge-account {
            display: none;
        }

        .merge-account-h5 {
            border: 1px solid red;
            display: block;
            overflow-y: scroll;

            &::-webkit-scrollbar {
                display: none;
            }

            // position: relative;

            // &::before {
            //     content: "";
            //     display: block;
            //     position: absolute;
            //     bottom: -2.6667vw;
            //     left: 0;
            //     width: 97%;
            //     height: 10px;
            //     box-shadow: .5333vw .5333vw 10.6667vw #fff;
            // }

            .challenge-h5 {

                .li {
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    font-size: 3.2vw;

                    span {
                        margin-top: 2.6667vw;
                    }

                    .r {
                        color: #fff;
                    }

                    &:first-child {
                        span {
                            margin-top: 0;
                        }

                        .r {
                            font-size: 3.2vw;
                            padding: 0 3.2vw;
                            height: 6.1333vw;
                            line-height: 6.1333vw;
                            color: #FF720E;
                            background: rgba(#FF720E, 0.2);
                            border-radius: 3.2vw;
                        }
                    }
                }
            }
        }

        .el-radio-group {
            width: 100%;

        }

        .el-radio {
            width: 100%;
            height: auto;
            --el-color-primary: #979797;
            margin-right: 0;
            margin-top: 4.8vw;

            &:not(:last-child) {
                border-bottom: 1px solid #515151;
                padding-bottom: 4.8vw;
            }
        }

        :deep(.el-radio__label) {
            width: 100%;
            font-size: 3.2vw;
        }

        :deep(.el-radio__inner::after) {
            background-color: #FF7700;
            width: 1.8667vw;
            height: 1.8667vw;
        }

        :deep(.el-radio__inner) {
            position: relative;
            top: -18.5vw;
            width: 3.2vw;
            height: 3.2vw;
        }

        :deep(.el-radio__inner:hover) {
            border-color: #FF7700;
        }

        .merge-btn {
            width: 100%;
            padding: 2.5vw;
            border-radius: 5em;
            box-sizing: border-box;
            margin: 5.3333vw auto;
            font-size: 4.2667vw;
        }
    }
}
</style>