<template>
    <publicMaskPopup ref="failPaymentRef" isCenter>
        <template #slot_mask>
            <div class="failPaymentMain">
                <img class="close" alt="" src="@/assets/images/icons/close.png" @click="failPaymentRef.maskHide" />
                <p class="title">{{ t("Battle.tips8") }}</p>
                <p class="text">{{ t("Battle.tips10") }}</p>
                <button class="btn" @click="bindDoRepay">{{ t("Battle.tips9", {
                    cost:
                        formatNumberWithCurrency(data.renewSignUpCost)
                }) }}</button>
            </div>
        </template>
    </publicMaskPopup>
    <publicWaitingPayment ref="publicWaitingPaymentRef"></publicWaitingPayment>
</template>
<script setup>
import { toRefs } from "vue";
import { formatNumberWithCurrency } from "@/utils/tools";
import { getAssetType } from "@/data/currencyFlag.js"
import { usePayment } from "@/hooks/usePayment"
const { bindDoRePayment } = usePayment()
const { t } = useI18n();
const props = defineProps({
    data: {
        type: Object,
        default: {}
    }
});

const { data } = toRefs(props);
const failPaymentRef = ref();
const publicWaitingPaymentRef = ref();
const submitBol = ref(false);
const shower = () => {
    failPaymentRef.value.maskShow();
};
defineExpose({ shower });
const bindDoRepay = _debounce(() => {
    //续费发起订单参数
    const params = {
        phaseIndex: data.value.phaseIndex,
        challengeUid: data.value.uid,
        localAmount: data.value.renewSignUpCost,
        localCurrency: data.value.currency,
        paymentType: 1
    };
    //续费发起支付参数
    const payParams = {
        amount: data.value.renewSignUpCost,
        baseCurrency: getAssetType(data.value.currency) === 1 ? "USD" : "USDT",
        targetCurrency: data.value?.localCurrency ? data.value.localCurrency : "USD",
        paymentType: 1,
        assetType: getAssetType(data.value.currency)
    };
    bindDoRePayment(params, payParams, () => {
        failPaymentRef.value.maskHide();
        publicWaitingPaymentRef.value.shower()
    })
}, 1000, { leading: true, trailing: false });
</script>
<style scoped lang="scss">
.failPaymentMain {
    width: 550px;
    height: auto;
    background-color: #000000;
    border-radius: 24px;
    padding: 20px 50px 60px;
    box-sizing: border-box;
    @include dfac;
    flex-direction: column;

    .close {
        width: 20px;
        height: 20px;
        position: absolute;
        top: 20px;
        right: 20px;
        cursor: pointer;
    }

    .title {
        @include setfont(30px, #ffffff, 700);
        line-height: 42px;
        margin-top: 40px;
    }

    .text {
        @include setfont(16px, #ffffff, 700);
        line-height: 24px;
        margin-top: 40px;
    }

    .btn {
        margin-top: 40px;
        @include dfjcac;
        @include setfont(14px, #ffffff, 700);
        border-radius: 5em;
        width: 350px;
        height: 42px;
        background: linear-gradient(90deg, #ff8922 0%, #ff6257 100%);
        cursor: pointer;
    }
}

@media screen and (max-width: 1024px) {
    .failPaymentMain {
        width: 87.4667vw;
        border-radius: 6.4vw;
        padding: 5.3333vw 5.3333vw 16vw;

        .close {
            width: 5.3333vw;
            height: 5.3333vw;
            top: 5.3333vw;
            right: 5.3333vw;
        }

        .title {
            font-size: 6.4vw;
            line-height: 1.5;
            margin-top: 5.3333vw;
        }

        .text {
            font-size: 3.7333vw;
            line-height: 1.5;
            margin-top: 3.2vw;
        }

        .btn {
            margin-top: 5.3333vw;
            font-size: 3.7333vw;
            width: 100%;
            height: 11.2vw;
        }
    }
}
</style>
